import React, { useEffect, useState } from 'react';
import { over } from 'stompjs';
import Stomp from 'stompjs';
import SockJS from 'sockjs-client';


let stompClient = null;

const generateUserId = () => {
  return 'User' + Math.floor(Math.random() * 1000);
};

function ChatComponent() {
  const [publicChats, setPublicChats] = useState([]);
  const [userData, setUserData] = useState({
    username: generateUserId(),
    connected: false,
    message: ''
  });

  useEffect(() => {
    console.log(userData);
  }, [userData]);

  const connect = () => {
    let Sock = new SockJS('http://localhost:8080/ws');
    stompClient = Stomp.over(Sock);

    stompClient.connect({ username: userData.username }, onConnected, onError);
  }

  const onConnected = () => {
    setUserData({ ...userData, "connected": true });
    userJoin();
  }

  const userJoin = () => {
    var chatMessage = {
      senderName: userData.username,
      status: "JOIN"
    };
    stompClient.send("/app/message", {}, JSON.stringify(chatMessage));
  }

  const onError = (err) => {
    console.log(err);
  }

  const handleMessage = (event) => {
    const { value } = event.target;
    setUserData({ ...userData, "message": value });
  }

  const sendValue = () => {
    if (!stompClient) {
      connect();
    } else {
      sendMessage();
    }
  };

  const sendMessage = () => {
    var chatMessage = {
      senderName: userData.username,
      message: userData.message,
      status: "MESSAGE"
    };
    console.log(chatMessage);
    stompClient.send("/app/message", {}, JSON.stringify(chatMessage));
    // setUserData({ ...userData, "message": "" });
  };

  const onMessageReceived = (payload) => {
    var payloadData = JSON.parse(payload.body);
    switch (payloadData.status) {
      case "JOIN":
        break;
      case "MESSAGE":
        setPublicChats(prevChats => [...prevChats, payloadData]);
        break;
    }
  }

  useEffect(() => {
    if (stompClient) {
      stompClient.subscribe('/chatroom/public', onMessageReceived);
    }
  }, [stompClient]);

  return (
    <div className="chat h-100 w-100">
      <div className="chat-title">
        <div style={{ fontSize: '1.3rem' }}>LEVEL GROUP</div>
        <div className="avatar">
          <img className='chat-img' alt="" />
        </div>
      </div>

      <div className="messages">
        <div className="messages-content">
          {publicChats.map((chat, index) => (
            <div className={`message ${chat.senderName === userData.username && "self"}`} key={index}>
              {chat.senderName !== userData.username && <div className="avatar">{chat.senderName}</div>}
              <div className="message-data">{chat.message}</div>
              {chat.senderName === userData.username && <div className="avatar self">{chat.senderName}</div>}
            </div>
          ))}
        </div>
      </div>

      <div className="message-box">
        <textarea
          type="text"
          className="message-input"
          placeholder="Type message..."
          value={userData.message}
          onChange={handleMessage}
          name="message"
        ></textarea>
        <button
          style={{
            backgroundColor: 'transparent',
            color: 'rgb(173, 129, 82)',
            fontSize: '1rem',
            fontWeight: '100',
            border: '1px solid rgb(173, 129, 82)'
          }}
          type="button"
          className="message-submit"
          onClick={sendValue}
        >
          Send
        </button>
      </div>
    </div>
  );
}

export default ChatComponent;